body {
  background-color: lightcyan;
}
.container {
  background-color: #cee5f2;
  display: flex;
  flex-wrap: wrap;
  margin: 10px;
  padding: 10px;
  list-style: none;
  width: 800px;
  border-radius: 10px;
}

.card {
  background-color: #accbe1;
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
}

.primaryInfo{
  font-weight: bold;
  font-size: 22px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.secondaryInfo{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.control{
  display: flex;
  justify-content: center;
}